body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: rgb(228, 228, 228); */
  background-image: linear-gradient(120deg, #DEDFE4,#8b8f9e);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.calc{
  width: fit-content;
  padding: 5px;
  background-color: rgb(255, 255, 255);
  background-image: linear-gradient(#41414B, #21212B);
  color: white;
}

.btn{
  width: 50px;
  height: 50px;
  margin: 5px;
  background-image: linear-gradient(120deg, #363b46,#898ea1);
  border-radius: 35%;
  box-shadow: inset 1px 1px 3px #afafaf;
  
}
.btn .in{
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: inherit;
  width: 100%;
  height: 100%;
  box-shadow: 5px 5px 10px #000000 ;
}
.btnAnimate{
  width: 50px;
  height: 50px;
  margin: 5px;
  background-image: linear-gradient(120deg, #363b46,#898ea1);
  border-radius: 35%;
  box-shadow: inset 1px 1px 3px #afafaf;
  transition: all 2s linear;
}
.btnAnimate .in{
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: inherit;
  width: 100%;
  height: 100%;
  box-shadow: 5px 5px 10px #000000 ;
}